.tip {
  position: absolute;
  background: #333;
  color: white;
  padding: 6px 10px;
  border-radius: 5px;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
  text-align: left;
  top: 0;
  left: 0;
  visibility: hidden;
  font-size:14px;
  max-width:180px;
  p{
    margin:0;
    font-size:13px;
  }
  @include breakpoint(phablet) {
    max-width:400px;
  }
}


